body {
  color: #333;
  background-color: #f7f7f7;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  background: #fff;
  border-radius: 4px;
  max-width: 900px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

input[type="password"] {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

button {
  cursor: pointer;
  margin-top: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

#artifactProgressContainer {
  margin-top: 10px;
}

#artifactProgressBar {
  width: 100%;
  height: 20px;
  display: block;
}

#artifactProgressText {
  color: #06c;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-top: 5px;
  font-weight: bold;
  display: block;
  overflow: hidden;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 8px;
}

.linkContainer {
  flex-direction: column;
  gap: 5px;
  margin-bottom: 10px;
  display: flex;
}

.linkProgressBar {
  width: 100%;
  height: 20px;
  display: block;
}

.linkProgressText {
  color: #06c;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: bold;
  display: block;
  overflow: hidden;
}
